import React from "react";

const About = () => {
  const [header] = React.useState({
    subHeader: "About Me",
    text:
      "Web developer and scalable coder, experienced in developing web apps based on React.js",
  });
  const [state] = React.useState([
    { id: 1, title: "Name:", text: "Md. Saheb Rana" },
    { id: 2, title: "Email:", text: "m.sahebrana@gmail.com" },
    { id: 3, title: "Phone:", text: "+8801781-129687" },
    { id: 4, title: "Linkedin", text: "in/sahebrana" },
  ]);
  return (
    <div className="about" id="about">
      <div className="container">
        <div className="common">
          <h1 className="mainHeader">{header.subHeader}</h1>
          <p className="mainContent">{header.text}</p>
          <div className="commonBorder">
          </div>
        </div>
        <div className="row  h-650 alignCenter">
          <div className="col-6">
            <div className="about__img">
              <img src="/images/rana.png" alt="Rana" />
            </div>
          </div>
          <div className="col-6">
            <div className="about__info">
              <h1>Hi There</h1>
              <div className="about__info-p1">
                I'm Md. Saheb Rana a professional full-stack developer experienced in React.js and Laravel.
              </div>
              <div className="about__info-p2">
                Currently I'm working as a Full-stack developer in Talent Pro.
                Previously I was on Web Developing as a freelancer on Fiverr.
                I love to learn new technologies and spreading knowledge.
              </div>
              <div className="info__contacts">
                <div className="row">
                  {state.map((info) => (
                    <div className="col-6">
                      <strong>{info.title}</strong>
                      <p>{info.text}</p>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
