import React from "react";
import {
  FaReact,
  FaLaravel,
  FaCss3,
  FaApple,
  FaGitAlt,
  FaFileSignature,
} from "react-icons/fa";
const Skill = () => {
  const [header] = React.useState({
    mainHeader: "SKILLS",
    subHeading: "My Skills",
    text:
      "My current expertise fields",
  });
  const [state] = React.useState([
    {
      id: 1,
      icon: <FaReact className="commonIcons" />,
      heading: "React",
      text:
        "I'm using React js as a front-end technology to manipulate outstanding interaction.",
    },
    {
      id: 2,
      icon: <FaLaravel className="commonIcons" />,
      heading: "Laravel",
      text:
        "I'm experienced Laravel to devlop back-end services as well as API",
    },
    {
      id: 3,
      icon: <FaCss3 className="commonIcons" />,
      heading: "Web Desing",
      text:
        "I'm used to applying Bootstrap and Tailwind CSS for responsive web design",
    },
    {
      id: 4,
      icon: <FaApple className="commonIcons" />,
      heading: "App Devlopment",
      text:
        "I'm experienced in React Native to develop Android and IOS apps",
    },
    {
      id: 5,
      icon: <FaGitAlt className="commonIcons" />,
      heading: "Version Controlling",
      text:
        "Experienced in Bitbucket, GitHub to merging and releasing tracking",
    },
    {
      id: 6,
      icon: <FaFileSignature className="commonIcons" />,
      heading: "Project Management",
      text:
        "I've worked on Jira and Confluence to manage project",
    },
  ]);
  return (
    <div className="skills" id = "skills">
      <div className="container">
        <div className="skills__header">
          <div className="common">
            <h3 className="heading">{header.mainHeader}</h3>
            <h1 className="mainHeader">{header.subHeading}</h1>
            <p className="mainContent">{header.text}</p>
            <div className="commonBorder"></div>
          </div>

          <div className="row bgMain">
            {state.map((info) => (
              <div className="col-4 bgMain">
                <div className="skills__box">
                  {info.icon}
                  <div className="skills__box-header">{info.heading}</div>
                  <div className="skills__box-p">{info.text}</div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Skill;
